.skills-overall-container{
    padding: 80px 0 0 0;
    background-color: var(Black);
    position: relative;
    width: 100%;
}

.skills-container{
    flex-wrap: wrap;
    gap: 15%;
    margin: 30px 5% 0 5%;
}

.skills-category {
    margin-bottom: 40px;
}

.category-heading {
    color: var(--PortGreen, #8FFF00);
    text-align: center;
    font-family: "Red Hat Mono";
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -1.25px;
    margin: 40px 0;
}

.skills-grid {
    flex-wrap: wrap;
    gap: 40px;
}

.skill-card {
    flex-direction: column;
    text-align: center;
}

.skill-icon {
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
}

.skill-name {
    color: var(--PortGrey2, #AAA);
    text-align: center;
    font-family: "Red Hat Mono";
    font-size: 19.39px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -0.97px;
}

@media (max-width:600px) {
    .skills-container{
        gap: 5%;
    }
    
    .skills-category {
        margin-bottom: 20px;
    }
    .skills-grid {
        flex-wrap: wrap;
        gap: 20px;
    }
    .skill-icon {
        height: 35px;
        width: 35px;
        margin-bottom: 10px;
    }

    .skill-icon > svg {
        height: 35px;
        width: 30px;
        object-fit: cover;
    }
    .skill-name {
        font-size: 15.39px;
    }
}

