.Spotlight-overall-container{
    background-image: url(../../assets/Spotlight/bg.png);
    margin-top: 20px;    
    width: 100vw;
    max-width: 100%; 
    height: 100vh;
    background-size: cover, auto, auto;
    background-repeat: no-repeat;
    background-position: center, left center, left center ; 
    flex-direction: column;
    overflow-y: hidden;
    overflow-x:hidden;
}

.title-text{
    z-index: 10;
    margin-top: 20px;
    color: var(--PortWhite, #FFF);
    text-align: center;
    font-family: "Red Hat Mono";
    font-size: 75.703px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -9.57px;
}

.namestyles{
    font-family: "Red Hat Mono";
    color: var(--PortGreen, #8FFF00);
}

.title-description{
    margin-top: -10px;
    color: #888;
    font-family: "Red Hat Mono";
    font-size: 48.639px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: -4.864px;
}

.spotlight-buttons-container{
    margin-top: 30px;
    width: 90%;
    gap: 5%;
}

.spotlight-download-button{
    border-radius: 38.29px;
    border: 1.276px solid var(--PortGrey, #CCC);
    gap: 10px;
    height: 55px;
    width: 220px;
}

.spotlight-download-button:hover{
    background-color: #0f0f0ffa;
}

.spotlight-download-button > p{
    color: var(--PortWhite, #FFF);
    font-family: "Red Hat Mono";
    font-size: 19.029px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.3px;
}

.downloadicon{
    height: 20px;
    width: auto;
}

.spotlight-contact-button{
    border-radius: 38.29px;
    background: var(--PortGreen, #8FFF00);
    gap: 10px;
    height: 56px;
    width: 221px;
}

.spotlight-contact-button:hover{
    background: #91ff00cc;
}

.spotlight-contact-button > p{
    color: #000;
    font-family: "Red Hat Mono";
    font-size: 19.029px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: -0.3px;
}

.contacticon{
    height: 16px;
    width: auto;
}

.spotlight-roles-container{
    gap: 15px;
    position: absolute;
    padding: 0 5%;
    top: 88vh;
}

.spotlight-roles{
    color: var(--PortWhite, #FFF);
    font-family: "Red Hat Mono";
    font-size: 26.267px;
    font-style: normal;
    text-align: center;
    font-weight: 400;
    line-height: 120%;
    letter-spacing: -0.424px;
}

.line-between{
    margin-top: -3px;
    color: var(--PortGreen, #8FFF00);
    font-family: "Red Hat Display";
    font-size: 28.267px;
    font-style: normal;
    font-weight: 800;
    line-height: 100%; 
    letter-spacing: -0.424px;
}


@media (max-width:1500px) {
    .spotlight-roles{
        font-size: 22.267px;
    }
}

@media (max-width:1050px) and (min-height:600px){
    .Spotlight-overall-container{
        background-image: url(../../assets/Spotlight/bg-iPad.png);
        background-size: cover, auto, auto;
        background-repeat: no-repeat;
        background-position: center, left center, left center ; 
    }
}

@media (max-width:600px) and (min-height:600px) and  (max-height:800px){
    .Spotlight-overall-container{
        background-image: url(../../assets/Spotlight/bg-phone.png);
        background-size: cover, auto, auto;
        background-repeat: no-repeat;
        background-position: center, left center, left center ; 
    }
}

@media (max-width:1200px) {
    .title-text{
        font-size: 70px;
    }

    .title-description{
        font-size: 45px;
        padding-top: 10px;
    }

    .spotlight-download-button{
        height: 55px;
        width: 200px;
    }

    .spotlight-download-button > p , .spotlight-contact-button > p{
        font-size:17.029px;
    }

    .spotlight-download-button > svg {
        height: 18px;
    }
    
    .spotlight-contact-button > svg{
        height: 14px;
    }

    .spotlight-contact-button{
        height: 56px;
        width: 200px;
    }

    .spotlight-roles{
        font-size: 18.267px;
    }
}

@media (max-width:900px){
    .Spotlight-overall-container{
        background-image: url(../../assets/Spotlight/bg-iPad.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center, left center, left center ; 
    }
    .title-text{
        font-size: 60px;
    }

    .title-description{
        font-size: 40px;
        padding-top: 8px;
    }

    .spotlight-download-button{
        height: 55px;
        width: 200px;
    }

    .spotlight-download-button > p , .spotlight-contact-button > p{
        font-size:17.029px;
    }

    .spotlight-download-button > svg {
        height: 18px;
    }
    
    .spotlight-contact-button > svg{
        height: 14px;
    }

    .spotlight-contact-button{
        height: 56px;
        width: 200px;
    }
    .spotlight-roles-container{
        gap:7px;
    }

    .spotlight-roles{
        font-size: 16px;
    }
}

@media (max-width:600px){
    .title-text{
        font-size: 45px;
    }

    .title-description{
        font-size: 30px;
        padding-top: 5px;
    }

    .spotlight-download-button{
        height: 40px;
        width: 140px;
        gap: 4px;
    }

    .spotlight-download-button > p , .spotlight-contact-button > p{
        font-size:14.029px;
    }

    .spotlight-download-button > svg {
        height: 16px;
    }
    
    .spotlight-contact-button > svg{
        height: 12px;
    }

    .spotlight-contact-button{
        height: 41px;
        width: 140px;
        gap: 4px;
    }
    .spotlight-roles-container{
        gap:5px;
    }

    .spotlight-roles{
        font-size: 13px;
        overflow: visible;
    }
}

@media (max-width:500px){
    .Spotlight-overall-container{
        background-image: url(../../assets/Spotlight/bg-phone.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center, left center, left center ; 
    }
}